import React, { ReactFragment } from "react";
import classNames from "classnames";
import { motion } from "framer-motion";
import { scrollEffectAnim } from "../../utilities/animationVariants";

import "./WorkCard.scss";

interface WorkCardProps {
  color?: "blue" | "rose" | "yellow" | "purple";
  img?: string;
  headline: ReactFragment | string;
  body: string | ReactFragment;
  button?: object;
  rightAlign?: boolean;
  altImage?: any;
  imgAltText?: string;
  bannerContent?: any;
  className?: string;
  imgWrapperClass?: string;
  imgClass?: string;
  textWrapperClass?: string;
}

const hoverAnimation = {
  scale: 1.02,
  transition: {
    type: "spring",
    duration: 0.05,
    bounce: 0.2,
    velocity: 10000,
  },
};

const Banner = (props: {
  bannerContent: any;
  rightAlign: boolean | undefined;
}) => {
  return (
    <motion.div
      variants={scrollEffectAnim}
      initial="offscreen"
      whileInView="onscreen"
      className={classNames(
        props.rightAlign && "right-align",
        "work-card__banner-wrapper"
      )}
    >
      <div className="work-card__banner py-3">
        <div className="work-card__banner-content px-4 pb-2">
          {props.bannerContent}
        </div>
      </div>
    </motion.div>
  );
};

const WorkCard = (props: WorkCardProps) => {
  return (
    <motion.div
      initial="offscreen"
      whileInView="onscreen"
      viewport={{ once: true, amount: 0.075 }}
      className={classNames(
        props.className && props.className,
        props.rightAlign && "right-align",
        props.bannerContent && "portfolio__work-card--with-banner",
        props.color === "blue" && "bg-gradient-blue--angle",
        props.color === "rose" && "bg-gradient-rose",
        props.color === "yellow" && "bg-gradient-yellow",
        props.color === "purple" && "bg-gradient-purple",
        "portfolio__work-card d-flex flex-wrap align-items-center px-3 px-md-4 px-lg-5"
      )}
    >
      {props.bannerContent && (
        <Banner
          bannerContent={props.bannerContent}
          rightAlign={props.rightAlign}
        />
      )}

      {props.rightAlign ? (
        <React.Fragment>
          {/* IMAGE */}
          <motion.div
            whileHover={hoverAnimation}
            className={classNames(
              props.imgWrapperClass
                ? props.imgWrapperClass
                : "work-card__img-wrapper order-1 order-xl-0",
              "col-12 col-xl-8 px-0 px-xl-5"
            )}
          >
            {props.altImage ? (
              props.altImage
            ) : (
              <motion.img
                variants={scrollEffectAnim}
                src={props.img}
                alt={props.imgAltText}
                className={classNames(
                  props.imgClass && props.imgClass,
                  "work-card__img w-100"
                )}
              />
            )}
          </motion.div>

          {/* TEXT */}
          <motion.div
            whileHover={hoverAnimation}
            className={classNames(
              props.bannerContent && "mt-xl-5 pt-xl-5 pt-xl-0",
              props.textWrapperClass && props.textWrapperClass,
              "work-card__text-wrapper col-12 col-xl-4 order-0 order-xl-1 ps-0 ps-xl-4 mt-5 mt-xl-0"
            )}
          >
            <motion.h4
              variants={scrollEffectAnim}
              className={classNames(
                props.bannerContent && "work-card__headline--shrink-width",
                "work-card__headline mb-4"
              )}
            >
              {props.headline}
            </motion.h4>

            <motion.p variants={scrollEffectAnim} className="max-w-xs mb-5">
              {props.body}
            </motion.p>

            {props.button && (
              <motion.div
                variants={scrollEffectAnim}
                className="text-center text-md-start mb-5"
              >
                {props.button}
              </motion.div>
            )}
          </motion.div>
        </React.Fragment>
      ) : (
        <React.Fragment>
          {/* TEXT */}
          <motion.div
            whileHover={hoverAnimation}
            className={classNames(
              props.bannerContent && "mt-xl-5 pt-xl-5 pt-xl-0",
              props.textWrapperClass && props.textWrapperClass,
              "work-card__text-wrapper col-12 col-xl-4 order-1 order-xl-0 mt-5 mt-xl-0"
            )}
          >
            <motion.h4
              variants={scrollEffectAnim}
              className={classNames(
                props.bannerContent && "work-card__headline--shrink-width",
                "work-card__headline mb-4"
              )}
            >
              {props.headline}
            </motion.h4>

            <motion.p variants={scrollEffectAnim} className="max-w-xs mb-5">
              {props.body}
            </motion.p>

            {props.button && (
              <motion.div
                variants={scrollEffectAnim}
                className="text-center text-xl-start mb-0 mb-xl-5"
              >
                {props.button}
              </motion.div>
            )}
          </motion.div>

          {/* IMAGE */}
          <motion.div
            whileHover={hoverAnimation}
            className={classNames(
              props.imgWrapperClass
                ? props.imgWrapperClass
                : "col-12 col-xl-8 px-0 px-xl-5",
              "work-card__img-wrapper order-0 order-xl-1"
            )}
          >
            {props.altImage ? (
              props.altImage
            ) : (
              <motion.img
                variants={scrollEffectAnim}
                src={props.img}
                alt="property search application UI"
                className={classNames(
                  props.imgClass && props.imgClass,
                  "work-card__img w-100"
                )}
              />
            )}
          </motion.div>
        </React.Fragment>
      )}
    </motion.div>
  );
};

export default WorkCard;
